@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@200;300&family=Lora&display=swap);
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'National 2', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body
{
  width: 100vw;
  padding: 0 3.5%;
  font-size: 16px !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Unwanted..... */
code
{
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

header
{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
    padding: 10px 20px;
    position: sticky;
    top: 0;
    width: 100%;
    height: 100px;
}

header .logo
{
    color: #333;
    letter-spacing: 1.5px;
}

header .logo a
{
    text-decoration: none;
    color: #333;
}

nav.nav-container
{
    display: flex;

}
nav.nav-container > .nav-list
{
    list-style: none;
    display: flex;
}

.nav-container > .nav-list .nav-item
{
    display: inline-block;
    font-size: 0.8em;
    font-weight: 600;
}

.nav-container > .nav-list .nav-item .nav-link
{
    color: #222;
    padding: 5px 15px;
    border-radius: 25px;
    -webkit-text-decoration-color: #777;
            text-decoration-color: #777;
    text-decoration-thickness: 1px;
    text-underline-offset: 5px;
}

.nav-container > .nav-list .nav-item a.nav-link:hover
{
    color: #f8f8f8;
    background-color: rgb(155, 221, 221);
}

.nav-container button.btn
{
    border: none;
    background: transparent;
    border-radius: 5px;
    padding: 10px;
    position: relative;
    right: 0;
    z-index: 99999;
}

.nav-container button.btn:hover
{
    cursor: pointer;
}

.nav-container button.btn::before,
.nav-container button.btn::after
{
    content: '';
    width: 20px;
    height: 2px;
    display: block;
    margin-bottom: 3px;
    background: #000;
    position: relative;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

.nav-container button.btn.active::before,
.nav-container button.btn.active::after
{
    top: 50%;
    left: 50%;
    margin: 0;
    -webkit-transform: rotate(45deg) translate(-65%, 280%);
            transform: rotate(45deg) translate(-65%, 280%);
}

.nav-container button.btn.active::after
{
    left: -50%;
    -webkit-transform: rotate(-45deg) translate(50%, -50%);
            transform: rotate(-45deg) translate(50%, -50%);
}

.hamburger-menu
{
    position: absolute;
    width: 100%;
    background: #fff;
    top: 70px;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    padding: 50px 0;
    -webkit-animation-duration: 2s !important;
            animation-duration: 2s !important;
}

.hamburger-menu > div
{
    width: 50% !important;
    margin-bottom: 20px;
    padding: 0 20px;
}

@media screen and (max-width:600px)
{
    .hamburger-menu > div
    {
        width: 100% !important;
    }
}

.hamburger-menu .hamburger-menu-nav .nav-list
{
    list-style: none;
}

.hamburger-menu .hamburger-menu-nav .nav-list > .nav-item .nav-link
{
    text-decoration: none;
    color: #666;
    padding: 5px;
    font-size: 3.2em;
    font-weight: 300;
    font-family: 'IBM Plex Serif', serif;
    letter-spacing: 2px;
    font-family: 'IBM Plex Serif', serif;
}

.hamburger-menu .hamburger-menu-nav-links
{
    display: flex;
    flex-wrap: wrap;
}

.hamburger-menu .hamburger-menu-nav-links .hamburger-menu-linkBx
{
    width: 50%;
    min-height: 250px;
}

.hamburger-menu .hamburger-menu-nav-links .hamburger-menu-linkBx h5
{
    color: #999;
    font-weight: 300;
    font-size: large;
}
.hamburger-menu .hamburger-menu-nav-links .hamburger-menu-linkBx > ul
{
    list-style: none;
    padding: 8px 3px;
}
.hamburger-menu .hamburger-menu-nav-links .hamburger-menu-linkBx > ul > li:not(:first-child)
{
    margin-top: 8px;
}

.hamburger-menu .hamburger-menu-nav-links .hamburger-menu-linkBx > ul > li > a
{
    text-decoration: none;
    color: #333;
    font-weight: 500;
}

@media screen and (max-width:700px)
{
    nav.nav-container > ul.nav-list
    {
        display: none;
    }
}


/* Link hover effect... */
li.magic
{
    position: relative;
    overflow: hidden;
}

li.magic::after
{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
    transition-duration: 2s;
    background: rgb(139, 153, 150);
    z-index: -1;
}

li.magic:hover a
{
    color: #fff !important;
}

li.magic:hover::after
{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
section.body-part
{
    margin: 1em auto 1.5em;
}

